import BbwzPlayerBaseView from "./BbwzPlayerBaseView";
import BbwzConstDefine from "../../data/BbwzConstDefine";
import BbwzData from "../../data/BbwzData";

export default class BbwzSelfPlayerView extends BbwzPlayerBaseView {
    private goldLbl: cc.Label;
    constructor(node: cc.Node, public chair: number) {
        super();
        this.setNode(node);
    }

    protected initView() {
        super.initView();
        this.goldLbl = <cc.Label>this.getComponent("label_gold", cc.Label);
        BbwzConstDefine.addCommonClick(this.node, "btn_cz", this.onRechargeClick, this);
        BbwzData.instance.playerChipsFlyPos[this.chair] = this.getCenterWorldPos();
    }

    public setGoldLbl(point: number) {
        this.goldLbl.string = Global.Toolkit.formatPointStr(point, true);
    }

    public onSit(data: any) {
        super.onSit(data);
        this.setGoldLbl(data.point);
    }

    private onRechargeClick() {
        BbwzConstDefine.playBtnSound();
        Global.UI.show("WndRecharge");
    }

    public getCenterWorldPos() {
        return this.headImgSp.node.parent.convertToWorldSpaceAR(this.headImgSp.node.position);
    }

    //返回头像之间点的世界坐标
    public getChipStartPos() {
        return this.node.parent.convertToWorldSpaceAR(this.node.position);
    }
}